@use "mixins/vendor-prefixes";
@use "variables";

//
// Wells
// --------------------------------------------------


// Base class
.well {
  min-height: 20px;
  padding: 19px;
  margin-bottom: 20px;
  background-color: variables.$well-bg;
  border: 1px solid variables.$well-border;
  border-radius: variables.$border-radius-base;
  @include vendor-prefixes.box-shadow(inset 0 1px 1px rgba(0,0,0,.05));
  blockquote {
    border-color: #ddd;
    border-color: rgba(0,0,0,.15);
  }
}

// Sizes
.well-lg {
  padding: 24px;
  border-radius: variables.$border-radius-large;
}
.well-sm {
  padding: 9px;
  border-radius: variables.$border-radius-small;
}
